.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: $colorShowData;
  overflow-y: auto;

  &__top {
    margin-bottom: 48px;
  }

  &__list {
    margin: 40px 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;

    font-weight: 600;
    font-size: 22px;
    line-height: 22px;
    color: $colorText_H2_H3;
    text-decoration: none;
    letter-spacing: 2px;
    text-transform: uppercase;
    transition: border-color 0.3s;

    @include hover (border-color, $colorText_H2_H3);
  }

  &__call {
    display: block;
    width: max-content;
    font-weight: 600;
    text-transform: uppercase;
    color: $colorText_H2_H3;
    text-decoration: none;

    &--phone {
      margin-bottom: 16px;
      font-size: 16px;
      line-height: 27px;
    }

    &--address {
      padding-bottom: 8px;
      border-bottom: 1px solid;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 1px;
    }
  }
}
